@import url(https://fonts.googleapis.com/css2?family=Varela&display=swap);

.tree{
    background-color: white;
    height: 80vh;
    margin: 2%;
    border-radius: 10px;
    border: 2px solid #272B2C;
    margin-top: 0.5%;
    padding-top: 0.5%;
    padding-bottom: 2%;
}
.currentCourseLabel{
    position:relative;
    margin:auto;
    left: 2%;
    text-align: left;
    color: #272B2C;
  }
.instructions{
    display: block;
    align-items: center;
    font-size: 1.5rem;
}
.fullscreenIcon{
    font-size: 1rem;
}

li{
    margin:2%;
}
body{
    background-color: #D0CEBB;
    overflow-x: hidden;
}
.course-explorer-navbar{
    background-color: #17293F;
    color: white;
}

.nav-dropdown{
    height: 500px;
    overflow-y: auto;
}
span{
    margin:1%;
}
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Varela', sans-serif;
}



.currentDepartmentLabel{
    text-align:left;
    margin-left:2%;
    font-size: 2rem;
}
